export function fillRectCtx(x, y, width, height, ctx, color) {
    if (width < 0) {
        x += width;
        width = -width;
    }
    if (height < 0) {
        y += height;
        height = -height;
    }
    if (color) {
        ctx.fillStyle = color;
    }
    ctx.fillRect(x, y, width, height);
}
export function colorify(n) {
    return `hsl(${n % 255}, 50%, 50%)`;
}
